body {
  margin: 0;
  padding: 0;
  font-family: 'Noto Sans', sans-serif;
  background: #fafafa;
}

*, *:before, *:after {
  box-sizing: border-box;
}
.Modal {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  z-index: 100;
}
.ModalBackdrop {
  background-color: rgba(0,0,0,0.55);
  width: 100%;
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ModalContent {
  background: white;
  border: 1px solid #eee;
  max-width: 768px;
  max-height: 512px;
  padding: 20px;
  overflow: auto;
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.confirmationAlert {
  width: 50px;
  height: 50px;
  stroke-width: 1px;
  color: #ff3434;
}

.confirmationText {
  font-size: 14px;
  margin-bottom: 30px;
  margin-top: 20px;
}

.confirmationButtons {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}
.HeaderContainer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  background: white;
  height: 45px;
  z-index: 10;
  border-bottom: 1px solid rgba(0,0,0,0.09);
}

.Header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  background: white;
  height: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  max-width: 680px;
  margin: auto;
  z-index: 15;
  border-bottom: 1px solid rgba(0,0,0,0.09);
}

.HeaderLogo {
  height: 27px;
  margin-right: 10px;
  margin-left: 10px;
}
.UserDetails {
  margin-bottom: 28px;
  font-size: 13px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.UserDetailSection {
  padding-right: 20px;
}
.UserEdit {
  align-self: flex-end;
}
.UserName {
  font-size: 23px;
  line-height: 40px;
  font-weight: bold;
}
.UserInfo {
	font-size: 14px;
  line-height: 24px;
  margin-top: 5px;
}
.UserLocation {
  font-size: 14px;
  line-height: 24px;
  color: rgba(0,0,0,0.4);
}
.UserPinned  {
  cursor: pointer;
  width: 28px;
  height: 28px;
  margin-top: 12px;
}
.UserPinned.pinned {
  color: #00aa7a;
  fill: #fff;
}
.UserPinned.unpinned {
  color: #ccc;
}
.articleTitle {
  color: #333;
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 4px;
  display: inline-block;
  font-weight: bold;
}
.articleAuthor {
  font-size: 13px;
  margin-top: 22px;
  color: rgba(0,0,0,.44);
}
.articleDescription {
  font-size: 15px;
  line-height: 22px;
  margin-top: 4px;
  color: rgba(0,0,0,.7);
}
.articleDate {
  font-size: 13px;
  line-height: 22px;
  color: rgba(0,0,0,.54);
}
.articleHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}
.articleDelete {
  font-size: 22px;
  color: rgba(0,0,0,0.74);
  cursor: pointer;
  outline: none;
}
.articleImageContainer {
	margin-bottom: 12px;
}
.articleImagePreview {
  object-fit: contain;
  margin-top: 5px;
  width: 100%;
  height: 100%;
}
.EmptyList {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 2px;
  margin-top: 35px;
  background: white;
}

.EmptyListMessage {
  font-size: 14px;
  padding-bottom: 24px;
}
.InvalidAddress {
  display: flex;
  height: 100vh;
  width: 100%;
  align-items: center;
  justify-content: center;
  text-align: center;
  line-height: 27px;
  flex-direction: column;
}

.InvalidAddress .alert {
  width: 50px;
  height: 50px;
  stroke-width: 1px;
  color: #ff3434;
  margin-bottom: 17px;
}

.InvalidAddress .name {
  font-family: monospace;
  font-size: 17px;
}

.InvalidAddress .message {
  max-width: 680px;
}

.InvalidAddress a {
  color: black;
}
.loaderContainer {
  margin: auto;
  font-size: 14px;
  max-width: 300px;
  text-align: center;
}

.spinner{
  width: 40px;
  height: 40px;
  border: 2px solid #f3f3f3;
  border-top:3px solid #f25a41;
  border-radius: 100%;
  margin: auto;
  -webkit-animation: spin 1.5s infinite cubic-bezier(0.68, -0.55, 0.27, 1.55);
          animation: spin 1.5s infinite cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

@-webkit-keyframes spin {
    from{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes spin {
    from{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }to{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.ShareProfile {
  width: 100%;
}

.ShareProfile .ShareProfileCard {
  font-size: 13px;
  background: white;
  width: 100%;
  border: 1px solid rgba(0,0,0,0.11);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.07);
  border-radius: 2px;
  padding: 15px;
  padding-top: 13px;
  margin-bottom: 20px;
}

.ShareProfile .ShareProfileContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.ShareProfile .ShareProfileContainer .ShareInput {
  flex-grow: 1;
  height: 37px;
  padding-left: 4px;
  padding-right: 8px;
  font-family: monospace;
  border: 1px solid rgba(0,0,0,0.11);
  border-right: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.ShareProfile .ShareButton {
  border-left: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border: 1px solid rgba(0,0,0,0.11);
  outline: 0;
}

.ShareProfile .ShareProfileMessage {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 18px;
}

.ShareProfile .ShareProfileTextMessage {
  margin-left: 4px;
}

.ShareProfile .ShareMessage {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transition-timing-function: ease;
          transition-timing-function: ease;
  opacity: 0;
  visibility: hidden; 
  color: #00aa7a;
  margin-top: 8px;
  display: inline-block;
  font-size: 12px;
}
.ShareProfile .ShareMessage.show {
  opacity: 1;
  visibility: visible; 
}
.FeedPage {
