.ButtonTab {
  text-align: center;
  display: inline-block;
  width: 100%;
  font-size: 12px;
  font-weight: bold;
  text-decoration: none;
  color: #777;
  padding: 12px;
  text-transform: uppercase;
  border-bottom: 2px solid rgba(0,0,0,0);
}

.ButtonTab.current {
  border-bottom: 2px solid #334;
  color: #334;
}

.ButtonTab.disabled {
  color: #d4d4d4;
  cursor: not-allowed;
}

.Button {
  height: 37px;
  cursor: pointer;
  line-height: 37px;
  padding: 0 16px;
  border: 1px solid rgba(0,0,0,.15);
  background-color: rgba(255, 255, 255, 0);
  border-radius: 4px;
  color: #03a87c;
  border-color: #03a87c;
  text-transform: uppercase;
  text-align: center;
  font-size: 13px;
}

.Button.Secondary {
  color: #828282;
  border-color: #828282;
}

.Button.Alert {
  color: #ff3434;
  border-color: #ff3434;
}

.Button[disabled] {
  color: #d0d0d0;
  border-color: #d0d0d0;
  cursor: not-allowed;
}

